.betterembed {
  position: relative;
  max-width: 550px;
  margin-left: auto;
  margin-right: auto;
}
.betterembed,
.betterembed * {
  box-sizing: border-box;
}
.betterembed a *,
.betterembed button * {
  pointer-events: none;
}
.betterembed a:hover, .betterembed a:focus,
.betterembed button:hover,
.betterembed button:focus {
  outline: none;
}
.betterembed.is-betterembed-remote-visible .betterembed-switch__hide,
.betterembed.is-betterembed-remote-visible .betterembed__embed {
  display: block;
}
.betterembed.is-betterembed-remote-visible .betterembed-switch__show,
.betterembed.is-betterembed-remote-visible .betterembed__body {
  display: none;
}

.betterembed-switch__hide {
  display: none;
}

.betterembed-switch,
.betterembed-switch__show {
  display: block;
}

.betterembed__top {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -5px 5px;
  align-items: baseline;
}
.betterembed__top > * {
  margin: 0 5px;
}

.betterembed__network {
  word-break: break-word;
  hyphens: auto;
  line-height: 1;
  text-transform: uppercase;
  flex: 1;
  min-width: 50%;
}

.betterembed-switch {
  padding: 0;
  cursor: pointer;
  display: flex;
  background-color: transparent;
  border: none;
  align-items: center;
}
.betterembed-switch:hover .betterembed__logo, .betterembed-switch:focus .betterembed__logo {
  transform: scale(1.1);
}
.betterembed-switch:active .betterembed__logo {
  transform: scale(1.05);
}
.betterembed-switch .betterembed__logo {
  display: block;
  margin-left: 5px;
  width: 23px;
  height: 23px;
  transition: transform 0.2s ease;
  will-change: transform;
}

.betterembed__body {
  background: #4c5559;
  color: #fff;
  display: flex;
  flex-direction: column;
}
.betterembed__body > :not(.betterembed__media) {
  margin: 15px 15px 0;
}
.betterembed__body > :last-child {
  padding-bottom: 15px;
}
.betterembed__body a {
  color: inherit;
  text-decoration: underline;
}
.betterembed__body a:hover, .betterembed__body a:focus {
  color: inherit;
  text-decoration: none;
}

.betterembed__media {
  order: -1;
  margin: 0;
}
.betterembed__media img {
  width: 100%;
  height: auto;
}

.betterembed__header {
  word-break: break-word;
  hyphens: auto;
}
.betterembed__header * {
  font-size: 1.5em;
  color: inherit;
  margin: 0;
}

.betterembed__text {
  word-break: break-word;
  hyphens: auto;
}

.betterembed__read-more {
  font-size: 80%;
  white-space: nowrap;
}

.betterembed__dialog {
  position: absolute;
  top: 40px;
  left: 15px;
  right: 15px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.84);
  color: #fff;
  text-align: center;
  pointer-events: none;
  transition: opacity 0.2s ease, transform 0.2s ease;
  opacity: 0;
  transform: translateY(-20px);
}
.is-betterembed-dialog-visible:not(.is-betterembed-remote-visible) .betterembed__dialog {
  opacity: 1;
  transform: translateY(0);
  pointer-events: all;
}
.betterembed__dialog p {
  text-align: left;
  margin: 0;
}
.betterembed__dialog p:not(:last-of-type) {
  margin-bottom: 1em;
}

.betterembed__button {
  display: inline-block;
  margin: 20px 5px 0;
  padding: 10px 20px;
  cursor: pointer;
  color: inherit;
  background-color: transparent;
  border: 1px solid transparent;
}

.betterembed__button--secondary:hover, .betterembed__button--secondary:focus {
  text-decoration: underline;
}

.betterembed__button--primary {
  border-color: #fff;
  transition: color 0.2s ease, background-color 0.2s ease;
}
.betterembed__button--primary:hover, .betterembed__button--primary:focus {
  background-color: #fff;
  color: #000;
}

.betterembed__footer {
  padding-top: 15px;
  border-top: 1px solid currentColor;
  overflow: auto;
}

.betterembed__author.betterembed__author {
  font-weight: bold;
  text-decoration: none;
}
.betterembed__author.betterembed__author:hover, .betterembed__author.betterembed__author:focus {
  text-decoration: underline;
}

.betterembed__embed {
  display: none;
}
.betterembed__embed > * {
  width: 100% !important;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}

